@function em($size, $context: 16) {
	@return ($size/$context) * 1em; }

@function img-url($image) {
	@return url('../img/#{$image}'); }

@mixin image($image) {
	background-image: img-url($image); }

@mixin t($what: all, $dur: 0.25s, $easing: ease) {
	transition: $what $dure $easing; }

@mixin smooth-scroll {
	-webkit-overflow-scrolling: touch; }

@mixin smooth-font {
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale; }

@mixin gpu {
	transform: translate3d(0,0,0); }


//ADAPTIVE BREAKPOINTS
@mixin md {
	@include r(1139) {
		@content; } }

@mixin sm {
	@include r(919) {
		@content; } }

@mixin xs {
	@include r(589) {
		@content; } }

//NO TOUCH INTERACTION
@mixin hover {
	.no-touch & {
		&:hover {
			@content; } } }
@mixin active {
	.no-touch & {
		&:active {
			@content; } }
	&:active {
		@content; } }

@mixin no-touch {
	.no-touch & {
		@content; }
	@content; }
