.header {
	padding-top: 50px; }

.btn-nav {
	position: relative;
	display: none;
	vertical-align: top;
	padding: 5px 10px 5px 40px;
	background: none;
	@include sm {
		display: inline-block; }
	@include hover {
		.btn-nav {
			&__icon {
				&:before {
					transform: translate3d(0, -3px, 0); }
				&:after {
					transform: translate3d(0, 3px, 0); } } } }
	&__icon {
		position: absolute;
		top: 16px;
		left: 0;
		height: 15px;
		&:before,
		&:after,
		i {
			position: absolute;
			left: 0;
			width: 27px;
			border-bottom: 1px solid $white; }
		&:before,
		&:after {
			content: "";
			@include gpu;
			transition: transform 0.175s ease; }
		&:before {
			top: 0; }
		&:after {
			bottom: 0; }
		i {
			top: 7px; } }
	&__text {
		display: block;
		color: $white;
		@include f('lt', 18, 40);
		text-transform: uppercase; } }

.menu {
	position: fixed;
	z-index: 1000;
	top: 0;
	left: 0;
	right: 0;

	color: $white;
	background: $gray;

	@include f('lt', 18, 40);
	text-transform: uppercase;
	a {
		color: inherit; }
	&__link {
		display: none;
		@include sm {
			display: inline-block;
			vertical-align: top; } } }

.nav {
	&__list {
		font-size: 0.1px;
		text-align: justify;
		&:after {
			content: "";
			display: inline-block;
			width: 100%; }
		li,
		a {
			display: inline-block;
			vertical-align: top; }
		a {
			padding: 5px 0;
			font-size: 18px; } } }
